var query
export default{
	data:function(){
		return {
			aaBoxClass:"",
			keyword:"",
			searchList:{},
			city:"厦门",
			cityGroup:{},
			zm:"A",
			zms:{},
			recList:{}
		}
	},
	onLoad:function(){
		query = uni.createSelectorQuery().in(this);
		this.getPage();
		this.city=this.app.getCity();
		
	},
	onPageScroll: function(e) {
		var that = this;
		//e.scrollTop
		var y=e.scrollTop;
		var zms=this.zms;
		for(var el in zms){
			if(zms[el]>y){
				that.zm=el;
				break;
			}
		}
	},
	methods:{
		search:function(){
			var that=this;
			setTimeout(function(){
				that.app.get({
					url:that.app.apiHost+"/index.php?m=city&a=search&city="+that.keyword,						
					success:function(res){
						that.searchList=res.data.list
					}
				}) 
			},30)
			
		},
		searchShow:function(){
			this.aaBoxClass="flex-col";
		},
		setCity:function(acity){
			this.app.setCity(acity);
			this.city=acity;
			uni.navigateBack();
		},
		setZm:function(zm){
			this.zm=zm;
			
			var y=this.zms[zm];
			uni.pageScrollTo({
				scrollTop:y
			})
		},
		getZmsy:function(){
			var that=this;
			var zms=this.zms;








			wx.createSelectorQuery().selectAll('.zms').boundingClientRect(function(rects){
				rects.forEach(function(rect){
					zms[rect.id.replace("zms","")] = rect.top;
				})
				console.log(zms);
			}).exec();
			

			
			
		},
		getPage:function(){
			var that=this;
			that.app.get({
				url:that.app.apiHost+"/index.php?m=city",
				success:function(res){
					that.cityGroup=res.data.list;
					var zms=Array();
					for(var i in that.cityGroup){
						zms[i]=0;
					}
					that.zms=zms;
					that.recList=res.data.recList;
					setTimeout(function(){
						that.getZmsy();
					},300)
				}
			})
		}
	}
}
